.TodoListItem {
    display: flex;
    align-items: center;
    padding: 1rem;
    margin-bottom: 1rem;
    background-color: white;
    border-radius: 15px;
    box-shadow: 0 2px 5px rgba(0,0,0,0.1);
  
    .checkbox {
      display: flex;
      flex: 1;
      align-items: center;
      cursor: pointer;
  
      .check-circle {
        width: 1rem;
        height: 1rem;
        border-radius: 50%;
        border: 2px solid #c671ff;
        display: flex;
        align-items: center;
        justify-content: center;
        margin-right: 0.5rem;
  
        svg {
          font-size: 1rem;
          color: white;
          display: none;
        }
      }
  
      .text {
        flex: 1;
        font-weight: 500;
      }
  
      &.checked {
        .check-circle {
          background-color: #be5cff;
          border-color: #be5cff;
  
          svg {
            display: block;
          }
        }
  
        .text {
          color: #adb5bd;
          text-decoration: line-through;
        }
      }
    }
  
    .remove {
      display: flex;
      align-items: center;
      font-size: 1.5rem;
      color: #767676;
      cursor: pointer;
      &:hover {
        color: #a318ff;
      }
    }
  }
