@font-face {
  font-family: 'Cafe24SsurroundAir';
  src: url('https://gcore.jsdelivr.net/gh/projectnoonnu/noonfonts_2105_2@1.0/Cafe24SsurroundAir.woff') format('woff');
  font-weight: normal;
  font-style: normal;
}

.TodoTemplate {
  font-family: 'Cafe24SsurroundAir';
  width: 1200px;
  margin: 4rem auto;
  border-radius: 4px;
  overflow: hidden;
  display: flex;
  flex-direction: column;
  box-shadow: 0 0 8px rgba(0, 0, 0, 0.04);

  .app-title {
    font-size: 2.5rem; /* 폰트 크기 증가 */
    text-align: center;
    padding: 1rem;
    .my-title {
      font-weight: bold;
      color: rgb(0, 0, 0);
      font-size: 1.5rem;
    }
    .todo-title {
      color: rgb(218, 124, 255);
      font-weight: bold;
    }
  }

  .main-section {
    display: flex;
    flex: 1;
    background-color: #f8f5fc;
    padding: 2rem;
    gap: 2rem;
  }

  .sidebar {
    flex: 1;
    display: flex;
    flex-direction: column;
    gap: 1rem;

    .section-title {
      font-size: 1.25rem;
      margin-bottom: 0.5rem;
      font-weight: 500;
    }

    .calendar-section,
    .reminders-section,
    .notes-section,
    .deadlines-section {
      background-color: #efe8fd;
      padding: 1rem;
      border-radius: 10px;
    }

    .calendar-section {
      flex: 2;
    }

    .reminders-section {
      flex: 1;
    }

    .react-calendar {
      width: 100%;
    }

    .reminders-textarea {
      height: 100px;
    }
    .reminders-textarea,
    .notes-textarea,
    .deadlines-textarea {
      width: 90%;
      padding: 0.5rem;
      border: 1px solid #efe7fe;
      border-radius: 4px;
      resize: none;
      line-height: 1.5;
      background-color: white;
      margin: 0 auto;
      display: block;
    }
  }

  .content-section {
    flex: 2;
    padding: 1rem;
    display: flex;
    flex-direction: column;
    gap: 1rem;
    background-color: #efe8fd;
    border-radius: 10px; // 모서리를 둥글게 만들어 다른 섹션들과 일치시킴

    .TodoInsert {
      margin-bottom: 1rem;
    }
  }
  .section-title {
    font-size: 1.25rem;
    margin-bottom: 0.5rem;
    font-weight: 00;
  }

  .right-sidebar {
    flex: 1;
    display: flex;
    flex-direction: column;
    gap: 1rem;
    height: 100%; // 높이를 100%로 설정
  }

  .notes-section,
  .deadlines-section {
    flex: 1;
    display: flex;
    flex-direction: column;
    background-color: #efe8fd;
    padding: 1rem;
    border-radius: 10px;
    height: calc(50% - 0.5rem);
    box-sizing: border-box;
  }
  .lined-textarea {
    background-image: linear-gradient(#dee2e6 1px, transparent 1px);
    background-size: calc(100% - 40px) 2rem;
    line-height: 2rem;
    padding: 0 20px;  /* 양옆 여백 */
  background-position: 20px 0;  /* 배경 위치 조정 */
  background-repeat: repeat-y;  /* 배경 세로 방향으로만 반복 */
  }
  .notes-textarea,
  .deadlines-textarea {
    width: 100%;
    height: calc(100% - 1rem); // 이 부분을 수정
    padding: 0.5rem;
    border: 1px solid #efe7fe;
    border-radius: 4px;
    resize: none;
    line-height: 1.5;
    background-color: white;
    font-size: 0.9rem;
    margin: 0 auto;
    display: block;
  }
}