.TodoInsert {
    display: flex;
    // background-color: #495057;

    input {
        outline: none;
        border: none;
        padding: 0.5rem;
        font-size: 1.25rem;
        line-height: 1.5;
        background-color: none;
        border-radius: 10px 0 0 10px;
        &::placeholder {
            color: #999999;
            font-family: 'Cafe24SsurroundAir';
        }

        flex: 1;
    }

    button {
        outline: none;
        border: none;
        padding-left: 1rem;
        padding-right: 1rem;
        color: white;
        font-size: 1.5rem;
        line-height: 1.5;
        border-radius: 0 10px 10px 0;
        background-color: #ffa4d2;

        display: flex;
        align-items: center;

        cursor: pointer;
        transition: 0.1s background-color ease-in;

        &:hover {
            background-color: #ff4ca8;
        }
    }
}