.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

/* 추가된 스타일링 */
.App {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  background-color: #f8f5fc;
  min-height: 100vh; /* 최소 높이 설정 */
}

.react-calendar {
  width: 100%; /* 부모 컨테이너의 폭에 맞추어 크기를 조정합니다. */
  max-width: 600px; /* 최대 폭을 설정합니다. 필요에 따라 조정 가능합니다. */
  font-size: 1rem; /* 기본 폰트 크기를 설정합니다. 필요에 따라 조정 가능합니다. */
  border: 1px solid #dee2e6; /* 캘린더의 테두리 설정 */
  border-radius: 10px; /* 테두리의 둥근 모서리 설정 */
}

.react-calendar__month-view__weekdays__weekday {
  padding: 0.5rem; /* 요일 영역의 패딩 조정 */
}

.react-calendar__month-view__days__day {
  width: 2.5rem; /* 날짜 영역의 폭을 조정 */
  height: 2.5rem; /* 날짜 영역의 높이를 조정 */
}

.react-calendar__tile {
  padding: 1rem; /* 날짜 영역의 패딩을 조정하여 크기를 확장합니다. */
  max-width: 100%;
  text-align: center;
  padding: 0.5rem 0;
  background: none;
  border: none;
  outline: none;
  cursor: pointer;
}

.react-calendar__tile--now {
  background: #ffb5df;
  border-radius: 4px;
}

.react-calendar__tile--active {
  background: #e692ff;
  border-radius: 4px;
  color: white;
}

